<template>
    <div class="p-3">
        <div v-for="section in sections" :key="section.name">
            <div v-if="section.books && section.books.length > 0">
                <h2 class="pt-5 pb-2">{{ section.name }}</h2>
                <div class="d-flex flex-wrap">
                    <div v-for="book in section.books" :key="book.code" class="d-flex flex-column align-items-center mb-4">
                        <div class="card m-2 btn btn-outline-secondary p-0" @click="$router.push('books/' + book.code)">
                            <img :src="require('../assets/img/book-cover/' + book.code + '.jpg')" class="card-img-top">
                            <div class="card-body d-flex flex-column">
                                <h6 class="card-title flex-fill">{{ book.name }}</h6>
                            </div>
                        </div>
                        <a :href="require('../assets/pdf/' + book.code + '.pdf')" target="_blank" class="btn themed-btn">Download</a>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import gurus from '../assets/js/parampara';

export default {
    name: 'Books',
    data() {
        return {
            sections: [],
            selectedBook: false
        }
    },
    created() {
        var vm = this;
        vm.sections.push(gurus[1]);
        vm.sections.push(gurus[0]);
        vm.sections.push({
            name: 'Useful Books',
            books: [{
                name: 'Kirttan Guide - 8th Edition',
                code: 'KirttanGuide'
            }, {
                name: 'Daily Service Life Guide',
                code: 'ServiceLife'
            }]
        })
    }
}
</script>
<style lang="scss" scoped>
.card {
    max-width: 150px;
    height: 280px;
}

.card-img-top {
    max-height: 200px;
    max-width: 150px;
}

.btn-outline-secondary:hover {
    .themed-btn {
        color: #fff !important;
    }
}
</style>